<template>
	<div class="CheckDetails">
		<header><i class="iconfont icon-xiangzuo1" @click="goPrev"></i><span>查看明细</span></header>
		<div class="container">
			<table cellspacing="0">
				<thead>
					<tr>
						<td  @click="SortArr('Consignment_CheckDetailsData','SerailNo','SortCondition1',$event)">序列号</td>
						<td  @click="SortArr('Consignment_CheckDetailsData','Sku','SortCondition2',$event)">商品代码</td>
						<td  @click="SortArr('Consignment_CheckDetailsData','CaseNo','SortCondition3',$event)">箱号</td>
						<td  @click="SortArr('Consignment_CheckDetailsData','Descr','SortCondition4',$event)">商品名称</td>
					</tr>
				</thead>
				<tbody>
					<tr v-for='(dataItem,index) in Consignment_CheckDetailsData'  :id="index"  :key='index'   @click="selectData(index)">
						<td>{{dataItem.SerailNo}}</td>
						<td>{{dataItem.Sku}}</td>
						<td>{{dataItem.CaseNo}}</td>
						<td>{{dataItem.Descr}}</td>
					</tr>
				</tbody>
			</table>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"CheckDetails",
		data(){
			return{
				Consignment_CheckDetailsData:[],//列表数据
				title:"收货汇总",
				detail_item:0,//默认为第一个(选择商品)
				ifSelect:false,//是否显示选择项
				SortCondition1:false,
				SortCondition2:false,
				SortCondition3:false,
				SortCondition4:false,
			}
		},
		mounted(){
			$("thead").find("td").append('<i class="iconfont"></i>')
		},
		created:function(){

			this.title = this.$route.query.title;

			this.ifSelect = this.$route.query.ifSelect;


			if(this.$store.state.Consignment_CheckDetailsData){

				var Consignment_CheckDetailsData = this.$store.state.Consignment_CheckDetailsData;

				this.Consignment_CheckDetailsData = Consignment_CheckDetailsData;
			}
		},
		methods:{
			//返回上一页
			goPrev:function(){

				var that = this;

				this.$router.push({
					name:"ConsignmentIndex",
					query:{
						InvoiceNum:that.$route.query.InvoiceNum,//发货单号
						CommodityCode:that.$route.query.CommodityCode,//商品代码
					}
				})
			},
			//点击选择具体订单
			selectData:function(idx){

				this.detail_item = idx;
			},
			// 点击确认按钮 跳转到 收货页面
			selectData_submit:function(){

				var that = this;

				this.$router.push({
					name:"TakeDelivery",
					query:{
						Summary:{
							descr:that.Consignment_CheckDetailsData[that.detail_item].descr,//商品名称
							qtyreceived:that.Consignment_CheckDetailsData[that.detail_item].qtyreceived,//已收数量
							qtyremain:that.Consignment_CheckDetailsData[that.detail_item].qtyremain,//待收数量
							sku:that.Consignment_CheckDetailsData[that.detail_item].sku,//商品代码
							susr2:that.Consignment_CheckDetailsData[that.detail_item].susr2,//单位
						}
					}
				})
			}
		}
	}
</script>
<style lang="less" scoped>
	.CheckDetails{
		&>.container{
			width:100%;
			padding-bottom: 130/50rem;
			table{
				tr{
					td:nth-child(1){
						min-width: 130/50rem;
					}
					td:nth-child(2){
						min-width: 160/50rem;
					}
					td:nth-child(4){
						min-width: 320/50rem;
					}
				}
			}
		}
	}
	@media screen and  (max-width: 332px) and (min-width: 251px){
		.CheckDetails{
			&>.container{
				table{
					tr{
						td:nth-child(1){
							min-width: 170/50rem;
						}
						td:nth-child(2){
							min-width: 200/50rem;
						}
						td:nth-child(4){
							min-width: 400/50rem;
						}
					}
				}
			}
		}
	}
	@media screen and (max-width: 250px){
		.CheckDetails{
			&>.container{
				table{
					tr{
						td:nth-child(1){
							min-width: 210/50rem;
						}
						td:nth-child(2){
							min-width: 260/50rem;
						}
						td:nth-child(4){
							min-width: 400/50rem;
						}
					}
				}
			}
		}
	}
</style>


